.site-closed {
  flex-grow: 1;
  color: #ffffff;
  background: #004775;
  padding: 5em 0;
  display: flex;
  flex-direction: column;
  .container{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__inner {
    flex-grow: 1;
    max-width: 36.5625em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0 auto;
  }
  &__logo {
    width: 10em;
    img {
      width: 100%;
    }
  }
  &__title {
    margin-top: 2em;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-top: 1.25em;
    p {
      line-height: 130%;
    }
    a {
      &:hover {
        color: #fcbd15 !important;
      }
    }
  }
}
