.order-placed {
  background-image: url("../img/order-placed__bg.png");
  position: relative;
  &__img {
    position: absolute;
    top: 15.625em;
    right: 2em;
    width: 51.875em;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .order-placed {
    padding-bottom: 0;
    &__img {
      position: static;
      width: 100%;
      margin-top: 2.5em;
    }
  }
}