.cart{
  padding: 1em 0 0;
  position: relative;
  z-index: 2;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 26em - 2.25em) 26em;
    gap: 2.25em;
  }
  &__items{
    margin-top: -1.25em;
  }
}

.cart-card{
  padding: 1.25em 0;
  border-bottom: solid 1px #cddbe3;
  display: grid;
  grid-template-columns: 18.75em calc(100% - 21em);
  gap: 2.25em;
  &__img{
    display: block;
    border-radius: .5em;
    @include imgCover(65.6%);
  }
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__title{
    max-width: 25em;
  }
  &__remove{
    margin-top: .75em;
  }
  .cart-card__info-remove,
  &__remove{
    flex-shrink: 0;
    width: .875em;
    @include center;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    margin-top: 1em;
    @include grid100gap(.5em);
    &-item{
      display: flex;
      align-items: center;
    }
    &-remove{
      margin-left: 1.5em;
    }
  }
  &__price{
    margin-top: 1em;
  }
}

.review-form,
.cart-form{
  padding: 2.5em 2em;
  background-color: #f7f7f7;
  border-radius: .5em;
  position: sticky;
  top: 1em;
  &__fields{
    @include grid100gap(1em);
    .fg{
      select,
      textarea,
      input{
        border-color: #fff;
      }
    }
  }
  &__btn{
    width: 100%;
    margin-top: 2em;
  }
  .m-form__hint{
    margin-top: 1em;
  }
}

.cart-form{
  margin-top: -6em;
  &__fields{
    margin-top: 1.25em;
  }
  &__sum{
    margin-top: 1.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

}

.cart-payment{
  margin-top: 1.25em;
  padding-bottom: 1.5em;
  border-bottom: solid 1px #a0c5db;
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
}


@media screen and (max-width: 992px){
  .cart{
    padding: 1.875em 0 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 2em;
    }
  }

  .cart-card{
    grid-template-columns: 18.75em calc(100% - 19.75em);
    gap: 1em;
  }

  .cart-form{
    margin: 0 0 0 -1.125em;
    width: calc(100% + 2.25em);
    padding: 2.5em 1.125em;
    border-radius: 0;
  }
}


@media screen and (max-width: 576px){
  .cart-card{
    grid-template-columns: 100%;
  }
}
