.page-top {
  &__wrap {
    max-width: 46.5625em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    text-align: center;
    margin: 0 auto;
  }
  &__text {
    p {
      color: #ffffff;
    }
  }
}

.certificate {
  position: relative;
  z-index: 2;
  padding-bottom: 5em;
  margin-top: -9.375em;
  &__cards {
    position: relative;
  }
  &-card2 {
    max-width: 39.6875em;
    display: block;
    color: #ffffff;
    position: relative;
    margin: 0 auto;
    &__img {
      position: relative;
      padding-bottom: 65%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
      }
    }
    &__desc {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 2.5em 3.75em;
    }
    &__price {
      margin-top: 0.5em;
      span {
        font-size: 3.5em;
        line-height: 105%;
        font-weight: 800;
      }
    }
    &__logo {
      width: 7.5em;
      margin-top: 1.25em;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  &__img {
    position: absolute;
    top: 10.9375em;
    left: 0;
    width: 13.75em;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    top: 0;
    right: 0;
    width: 13.75em;
    img {
      width: 100%;
    }
  }
  &-form {
    max-width: 53.75em;
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
    background: #f7f7f7;
    padding: 3.75em;
    margin: 0 auto;
    margin-top: 6.25em;
    &__grid {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 2.25em / 2));
      gap: 2.25em;
    }
    &__wrap {
      display: grid;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__group {
      input,
      textarea {
        width: 100%;
      }
    }
    &__title {
      font-weight: 700;
    }
    &__flex {
      display: flex;
    }
    &__btn {
      flex-shrink: 0;
      margin-right: 1.5625em;
    }
    &__info {
      line-height: 120%;
      a {
        color: #689dc5;
      }
    }
  }
}


@media screen and (max-width: 992px) {
  .certificate {
    padding-bottom: 2.5em;
    margin-top: -5em;
    &-card2 {
      max-width: 25em;
      &__desc {
        padding: 1.25em 2em 2.5em;
      }
      &__title {
        font-size: 1.125em;
      }
      &__price {
        span {
          font-size: 2em;
        }
      }
      &__logo {
        width: 5em;
      }
    }
    &__images {
      display: none;
    }
    &-form {
      padding: 2.5em 1.25em;
      margin-top: 2.5em;
    }
  }
}

@media screen and (max-width: 576px) {
  .page-top {
    &__wrap {
      text-align: start;
    }
  }
  .certificate {
    &-form {
      gap: 2.25em;
      &__grid {
        grid-template-columns: 100%;
      }
      &__flex {
        display: block;
      }
      &__btn {
        width: 100%;
        margin: 0 0 2.25em 0;
      }
    }
  }
}
