.blog{
  margin-top: -5.625em;
  position: relative;
  z-index: 2;
  &__items{
    @media screen and (min-width: 993px){
      grid-template-columns: repeat(6,calc(100% / 6 - 2.25em * 5 / 6));
      .article-card{
        grid-column: span 2;
        &._big{
          grid-column: span 3;
          .article-card{
            &__content{
              display: flex;
              justify-content: space-between;
            }
            &__date{
              margin: 0 0 0 2em;
              white-space: nowrap;
            }
          }
        }
      }
    }
  }
  &__more{
    margin-top: 3.75em;
    display: flex;
    justify-content: center;
    .m-btn{
      padding: 0 1.6875em;
    }
  }
}

.article-top{
  background-size: cover;
  background-position: center;
  color: #fff;
  &__inner{
    padding: 10em 0 3.5em;
    position: relative;
    z-index: 2;
    min-height: 41.25em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__content{
    max-width: 62.5em;
  }
  &__subtitle{
    margin-bottom: 1.25em;
  }
}

.article{
  padding: 2.5em 0 0;
  .text{
    max-width: 53.75em;
    h3{
      margin-bottom: .6em;
    }
  }
  &__title{
    font-size:  1.5em;
    line-height: 130%;
    margin-bottom: 2em !important;
  }
}

.article-img{
  margin: 1.5em 0 2.5em;
  @include grid100gap(1em);
  img{
    width: 100%;
    display: block;
    border-radius: .5em;
  }
}


@media screen and (max-width: 992px){
  .blog{
    padding: 1.875em 0 0;
    margin: 0;
    &__more{
      display: none;
    }
  }

  .article-top{
    &__inner{
      padding: 13em 0 2em;
      min-height: $vh;
    }
    &__subtitle{
      margin-bottom: 1em;
    }
    &__date{
      display: none;
    }
  }

  .article{
    padding-top: 2em;
  }
}
