.server-error {
  flex-grow: 1;
  color: #ffffff;
  background: #004775;
  padding: 5em 0;
  display: flex;
  flex-direction: column;
  .container{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__inner {
    flex-grow: 1;
    max-width: 61.875em;
    display: flex;
    align-items: center;
    margin: 0 auto;
  }
  &__img {
    width: 22.5em;
    margin-right: 2.8125em;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: calc(100% - 25.3125em);
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
  }
  &__subtitle {
    font-size: 33px;
    line-height: 120%;
    font-weight: 700;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
  }
}

@media screen and (max-width: 992px) {
  .server-error {
    &__inner {
      display: block;
    }
    &__img {
      width: 15.625em;
      margin: 0 0 2.5em 0;
    }
    &__desc {
      width: 100%;
    }
  }
}
