.order-error {
  flex-grow: 1;
  color: #ffffff;
  background-image: url("../img/order-error__bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 5.625em 0;
  display: flex;
  flex-direction: column;
  .container{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__inner {
    flex-grow: 1;
    max-width: 32.5em;
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    margin-top: 2em;
  }
  &__btn {
    display: inline-flex;
    margin-top: 2.5em;
  }
}

@media screen and (max-width: 992px) {
  .order-error {
    padding: 6.25em 0;
  }
}
