.mistake {
  flex-grow: 1;
  color: #ffffff;
  background: #004775;
  padding: 5em 0;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__img {
    width: 22.5em;
    img {
      width: 100%;
    }
  }
  &__text {
    margin-top: 3.125em;
    p {
      font-size: 2.0625em;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__btn {
    padding: 0 1.4375em;
    margin-top: 3.125em;

  }
}

@media screen and (max-width: 992px) {
  .mistake {
    &__img {
      width: 15.625em;
    }
    &__text {
      margin-top: 2.5em;
      p {
        font-size: 1.625em;
      }
    }
    &__btn {
      margin-top: 2.5em;
    }
  }
}
