.popular-questions{
  padding: 2.5em 0;
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4.25em / 3));
    gap: 2.125em;
    align-items: flex-start;
    margin-top: 2em;
  }
  &__item{
    @include grid100gap(.5em);
    .text22{
      line-height: 119%;
    }
  }
  &__items2{
    margin-top: 2.25em;
    max-width: 53.5em;
  }
}


@media screen and (max-width: 992px){
  .popular-questions{
    padding-top: 1.875em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 1.875em;
      margin-top: 1.875em;
    }
    ._hide-tablet{
      display: none;
    }
    &__items2{
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .popular-questions{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
