.contacts-top{
  &__inner{
    color: #fff;
    margin-top: 2.875em;
    display: flex;
    justify-content: space-between;
  }
  &__socials{
    margin: 0;
    width: 18.75em;
    max-width: 100%;
  }
}

.contacts{
  flex-grow: 1;
  margin-top: -4.375em;
  display: grid;
  grid-template-columns: 43.4% 56.6%;
  min-height: 36.875em;
  &__content{
    max-width: 46.625em;
    padding-left: 7em;
    padding-top: 9.25em;
  }
}

.map{
  width: 100%;
  height: 120%;
  margin-top: -20%;
}

.contacts-form{
  margin-top: 2em;
  &__fields{
    grid-auto-flow: dense;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.125em));
    gap: 1.25em 2.25em;
  }
  ._textarea{
    @media screen and (min-width: 577px){
      grid-column: 2/3;
      grid-row: 1/4;
      textarea{
        height: 100%;
      }
    }
  }
  &__bottom{
    margin-top: 1.25em;
    display: flex;
  }
  &__btn{
    margin-right: 1.25em;
  }
}

.m-form__hint{
  a{
    color: $secondary;
  }
  .text16{
    line-height: 119%;
  }
}


@media screen and (max-width: 992px){
  .contacts-top{
    padding-bottom: 3.5em;
    overflow: hidden;
    &__inner{
      margin-top: 1.5em;
      @include grid100gap(1.25em);
    }
  }

  .contacts{
    margin: 0;
    padding: 1.875em 0 0;
    grid-template-columns: 100%;
    gap: 2.75em;
    .map{
      order: 2;
      height: 22.5em;
      margin: 0;
    }
    &__content{
      padding: 0 1.25em;
    }
  }

  .contacts-form{
    margin-top: 2.125em;
  }
}


@media screen and (max-width: 576px){
  .contacts-form{
    &__fields{
      grid-template-columns: 100%;
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      order: 2;
      margin: 1.875em 0 0;
    }
  }
}
