.review-top{
  padding-bottom: 7.625em;
  &__inner{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__hint{
    color: #fff;
    max-width: 49.25em;
    @include grid100gap(.5em);
  }
}

.reviews-collection{
  margin-top: -3.125em;
  position: relative;
  z-index: 2;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 26em - 2.25em) 26em;
    gap: 2.25em;
  }
  &__items{
    @include grid100gap(1.25em);
  }
  &__new{
    display: none;
  }
}

.review-form{
  &__top{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .review-top{
    padding-bottom: 3.5em;
    &__inner{
      display: block;
    }
    &__hint{
      margin-top: 2em;
    }
  }

  .reviews-collection{
    padding: 1.75em 0 0;
    margin: 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__new{
      display: flex;
      width: 100%;
      margin-bottom: 1.25em;
    }
  }

  ._open-review-form{
    .review-form{
      transform: translateX(0);
    }
    .header{
      z-index: -1;
    }
  }

  .review-form{
    position: fixed;
    top: 0;
    border-radius: 0;
    padding: 2.25em 1.25em;
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 1.875em;
    }
  }
}
