.text48{
  font-size: 3em;
  line-height: 110%;
}

.text46{
  font-size: 2.875em;
  line-height: 110%;
}


.text h2,
.text26{
  font-size: 1.625em;
  line-height: 123%;
}

.text24{
  font-size: 1.5em;
  line-height: 118%;
}

.text h4,
.text22{
  font-size: 1.375em;
  line-height: 110%;
}

.text20{
  font-size: 1.25em;
  line-height: 135%;
}

.text18{
  font-size: 1.125em;
  line-height: 140%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text13{
  font-size: .8125em;
  line-height: 150%;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.blue-text{
  color: $secondary;
}

.red-text{
  color: $red;
}

.text{
  h2,h3,h4{
    font-weight: 600;
  }
  h2{
    margin-bottom: .75em;
  }
  h4{
    margin-bottom: .875em;
  }
  h3{
    font-size: 1.5em;
    line-height: 130%;
    max-width: 30em;
    margin-bottom: 2em;
  }
  li,
  p{
    font-size: 1.125em;
    line-height: 133%;
  }
  p{
    @include lastM(0 0 1em);
  }
  hr{
    height: 1.25em;
    opacity: 0;
    display: block;
    margin: 0;
  }
  .gallery{
    margin-top: 2.5em;
    padding-bottom: 2.5em;
  }
  blockquote{
    margin: 2.5em 0;
    padding: 2.375em 3.75em;
    background-color: #f7f7f7;
    border-radius: 2.5em;
  }
  ul{
    margin: 1.875em 0;
    @include grid100gap(.5em);
    padding-left: 3em;
    li{
      padding-left: 3em;
      position: relative;
      &:before{
        content: '—';
        position: absolute;
        left: 0;
        top: 0;
        color: $secondary;
      }
    }
  }
  table{
    width: 100%;
    margin: 2.5em 0;
    th,
    td{
      height: 4.375em;
      padding: 0 1.625em;
      span{
        font-size: 1.125em;
        line-height: 133%;
      }
    }
    th{
      background-color: #f9f9f9;
    }
    tbody tr{
      &:nth-child(even) td{
        background-color: #f9f9f9;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .text46{
    font-size: 2.25em;
    line-height: 117%;
  }

  .text24-tablet{
    font-size: 1.5em;
    line-height: 170%;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text {
    p {
      @include lastM(0 0 .33em);
    }
  }
}


@media screen and (max-width: 576px){
  .text{
    ul{
      padding-left: 0;
    }
    blockquote{
      padding: 1.75em 1.25em;
    }
  }

  .m-table{
    width: calc(100% + 2.25em);
    margin: 2.5em 0 2.5em -1.125em;
    padding: 0 1.125em 1em;
    overflow-x: auto;
    table{
      margin: 0;
      min-width: 30em;
    }
  }
}
